import React from 'react';
import * as d3 from 'd3';
import '../css/Text.css';

export default class TextChart extends React.Component {
	constructor(props) {
      super(props)
      
      this.anchor = React.createRef();
    }

    componentDidMount() {
    	this.setBar()
    	this.updateAvgDelay();
    }

    componentDidUpdate() {
    	
    	this.updateAvgDelay();
    }

    updateAvgDelay() {
    	const data = this.props.text;
    	console.log(data);

    	const g = d3.select('#text-g')
    	var t = d3.transition()
		  .duration(750);

		// JOIN new data with old elements.
		var text = g.selectAll("text")
		.data(data, function(d) { return d.split("\n"); });

		// EXIT old elements not present in new data.
		text.exit()
		  .attr("class", "exit")
		.transition(t)
		  .attr("y", 60)
		  .style("fill-opacity", 1e-6)
		  .style('fill', "brown")
		  .remove();

		// UPDATE old elements present in new data.
		text.attr("class", "update")
		  .attr("y", 0)
		  .style("fill-opacity", 1)
		  .style('fill', "#333")
		.transition(t)
		  .attr("x", function(d, i) { return i * 32; });

		// ENTER new elements present in new data.
		text.enter().append("text")
		  .attr("class", "enter")
		  .attr("dy", ".35em")
		  .attr("y", -60)
		  .attr("x", function(d, i) { return i * 32; })
		  .style("fill-opacity", 1e-6)
		  .style('fill', "green")
		  .style('font', "bold 20px monospace")
		  .text(function(d) { return d; })
		.transition(t)
		  .attr("y", 0)
		  .style("fill-opacity", 1);

    }

    setBar() {
    	const data = this.props.text;
    	console.log(data);

    	const width = 1200;
    	const height = 100;

		var svg = d3.select(this.anchor.current)
					.append('svg')
					.attr('width', width)
					.attr('height', height)
					.attr("id", 'svg-text')
		    
		const g = svg.append("g")
					.attr("transform", "translate(32," + (height / 2) + ")")
					.attr("id", "text-g");
		
    }

    

    render() {
    	return(
    		<React.Fragment>
    		<div ref={this.anchor} />
    		</React.Fragment>
    	);
    }
}